.root {
  position: fixed;
  top: 0;
  width: 100%;
}

.bar {
  max-width: 100%;
  height: 64px;
  padding: 12px 16px;
  background-color: #000;
  color: #fff;
  display: flex;
  justify-content: space-between;
}

.headerContainer {
  display: grid;
  align-items: center;
}

.header {
  font-family: 'Cinzel Decorative', sans-serif;
  margin: 0;
  font-size: 1.5rem;
}

.headerLinkLarge {
  display: none;
}

.headerLinkSmall {
  text-decoration: none;
  color: #fff;
}

.profile {
  background-color: #000;
  border: none;
  display: flex;
  cursor: pointer;
}

.profileText {
  font-size: 1.025rem;
  margin: auto 0;
  display: grid;
}

.textTop, .textBottom {
  font-family: 'Quattrocento Sans', Arial, Helvetica, sans-serif;
  font-size: 1.025rem;
  color: #fff;
  text-align: right;
  margin: auto 0;
}

.textTop {
  margin-bottom: 2px;
  vertical-align: bottom;
}

.textBottom {
  margin-top: 2px;
  vertical-align: top;
}

.avatar {
  height: 52px;
  width: 52px;
  border-radius: 50%;
  box-shadow: 0px 0px 12px #fff;
  margin: auto 0 auto 24px;
}

.logoutDropdown {
  display: inline-block;
  position: fixed;
  top: 90px;
  right: 16px;
}

.hidden {
  display: none;
}

@media (min-width: 601px) {
  .headerLinkSmall {
    display: none;
  }

  .headerLinkLarge {
    text-decoration: none;
    color: #fff;
    display: inline;
  }
}

@media (min-width: 769px) {
  .profileText {
    font-size: 1.125rem;
  }

  .bp {
    display: none;
  }
}
